<template>
  <section>
      <h1 class="text-blue">Spøl</h1>

    <b-list-group>
        <b-list-group-item class="cursor-pointer" v-on:click="selectGame('velrottutolini')">Vel røttu tølini</b-list-group-item>
        
    </b-list-group>

  </section>
</template>

<script>
export default {
    name: 'GamesSelectorComponent',
    data() {
        return {
        selectedGame: ''
        }
    },
    methods: {
        /* eslint-disable */
    // Define the method that emits data to the parent as the first parameter to `$emit()`.
    // This is referenced in the <template> call in the parent. The second parameter is the payload.
    /*eslint no-unused-vars: "ignore"*/
    emitToParent (event) {
      this.$emit('selectGame', this.selectedGame)
    },

    selectGame(x){
        this.selectedGame = x;
        this.emitToParent(x);
    }
  }
}
</script>

