<template>
<section>

  <h1 class="text-blue">Vel røttu tølini</h1>
    <section class="mt-2">
        <p>Set røttu tølini inn fyri at fáa talið:</p>
    </section>

    <section class="my-2 mx-n2 text-center h1 py-2 bg-dark text-white">{{targetAsText}}</section>
    <b-row>
        <b-col><scroll-picker :options="options" v-model="tal1" /></b-col>
        <b-col><scroll-picker :options="options" v-model="tal2" /></b-col>
        <b-col><scroll-picker :options="options" v-model="tal3" /></b-col>
    </b-row>
    <section class="my-3" v-if="sumSelectedNumbers != 0">

        <h3>{{sumSelectedNumbers}}</h3>
        <h3 :class="[selectedNumberIsCorrect ? 'text-success' : '']">{{selectedNumberAsText}}</h3>
        <b-button v-if="selectedNumberIsCorrect" @click="doOver">Royn aftur</b-button>
    </section>
</section>
</template>

<script>

import TalTilOrd from '../../plugins/TalTilOrd'
export default {
    name: 'NumbersFromLetterGame',
    
    data(){
        return {
            dragSensitivity: 1.7,
            touchSensitivity: 1.7,
            scrollSensitivity: 1,
            options: [
                {value: 0, name: "0"},
                {value: 1, name: "1"},
                {value: 2, name: "2"},
                {value: 3, name: "3"},
                {value: 4, name: "4"},
                {value: 5, name: "5"},
                {value: 6, name: "6"},
                {value: 7, name: "7"},
                {value: 8, name: "8"},
                {value: 9, name: "9"},
            ],
            tal1: 0,
            tal2: 0,
            tal3: 0,
            targetValue: 0,      
        }
    },
    computed:{
        targetAsText(){
            return TalTilOrd.toWords(this.targetValue);
        },
        sumSelectedNumbers(){
            return parseInt("" + (this.tal1 ? this.tal1 : 0) + "" + (this.tal2 ? this.tal2 : 0) + "" + (this.tal3 ? this.tal3 : 0));
        },
        selectedNumberAsText(){
            return TalTilOrd.toWords(this.sumSelectedNumbers);
        },
        selectedNumberIsCorrect(){
            return this.sumSelectedNumbers == this.targetValue;
        }
        
    },
    mounted(){
        this.setTarget();
    },
    methods:{
        doOver(){
            this.setTarget();
            this.tal1 = 0;
            this.tal2 = 0;
            this.tal3 = 0;
        },
        setTarget(){
            this.targetValue = Math.floor(Math.random() * (999 - 100) + 100);
        }
    }
}
</script>

<style>
.vue-scroll-picker {
    position: relative;
    width: 100%;
    height: 5em!important;
    overflow: hidden;
    font-size: 2em;
}

 .vue-scroll-picker-layer .top {
    box-sizing: border-box;
    border-bottom: 1px solid #c8c7cc;
    background: linear-gradient(180deg, #f67bac 10%, rgb(246 123 172 / 23%))!important;
    top: 0;
    left: 0;
    right: 0;
    height: calc(50% - 1em);
    cursor: pointer;
}

.vue-scroll-picker-layer .middle {
    top: calc(50% - 1em);
    left: 0;
    right: 0;
    bottom: calc(50% - 1em);
    background: rgba(244,222,222, 0.3);
}

.vue-scroll-picker-layer .bottom {
    border-top: 1px solid #c8c7cc;
    background: linear-gradient(0deg, #f77cad 10%, rgb(246 123 172 / 75%))!important;
    bottom: 0;
    left: 0;
    right: 0;
    height: calc(50% - 1em);
    cursor: pointer;
}
</style>

