<template>
  <div>
   
      <div role="group" class="text-right tekst-input" :class="[talInFous == 0 ? 'aktivt' : '']" v-on:click="setFocus(0)">
        <!-- This is a form text block (formerly known as help block) -->
        <b-form-text id="input-live-help strong">{{fyrstaTalAsText}}</b-form-text>
        <money class="text-right border-0 p-0 text-white" disabled style="background:none;" v-model="fyrstaTal" v-bind="money"></money>
       

        

      </div>

      <div class="calcTypeIndicator">
        <span v-html="calcTypeText"></span>
      </div>


      <div role="group" class="text-right tekst-input" :class="[talInFous == 1 ? 'aktivt' : '']" v-on:click="setFocus(1)">
        <!-- This is a form text block (formerly known as help block) -->
        <b-form-text id="input-live-help strong">{{nastaTalAsText}}</b-form-text>
        <money class="text-right border-0 p-0  text-white" disabled style="background:none;" v-model="nastaTal" v-bind="money"></money>
       
       

      </div>

      <div class="calcResultIndicator" >
        <span>=</span>
      </div>
      <div role="group" class="text-right tekst-input mt-1">

        <money class="text-right border-0 p-0  text-white" disabled style="background:none;" v-model="result" v-bind="money"></money>

       

        <!-- This is a form text block (formerly known as help block) -->
        <b-form-text id="input-live-help strong">{{resultAsText}}</b-form-text>
      </div>





  <b-row no-gutters class="my-1">
    <b-col cols="6" class="px-2">
      <b-button block class="p-3 blue-knapp" @click="ruddaAlt">Rudda</b-button>
    </b-col>
    
    <b-col class="px-2"><b-button block class="p-3 blue-knapp" @click="removeOne"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-backspace-fill" viewBox="0 0 16 16">
  <path d="M15.683 3a2 2 0 0 0-2-2h-7.08a2 2 0 0 0-1.519.698L.241 7.35a1 1 0 0 0 0 1.302l4.843 5.65A2 2 0 0 0 6.603 15h7.08a2 2 0 0 0 2-2V3zM5.829 5.854a.5.5 0 1 1 .707-.708l2.147 2.147 2.146-2.147a.5.5 0 1 1 .707.708L9.39 8l2.146 2.146a.5.5 0 0 1-.707.708L8.683 8.707l-2.147 2.147a.5.5 0 0 1-.707-.708L7.976 8 5.829 5.854z"/>
</svg></b-button></b-col>
    <b-col class="px-2">
      <b-button block class="blue-knapp p-2 storan-tekst"  :class="[slag == 3 ? 'gul-knapp' : '']" v-on:click="changeCalcType(3)">&divide;</b-button>
    </b-col>
  </b-row>

  <b-row no-gutters class="my-1">
    <b-col class="px-2"><b-button block class="blue-knapp p-2 storan-tekst" @click="addDigit(7)">7</b-button></b-col>
    <b-col class="px-2"><b-button block class="blue-knapp p-2 storan-tekst" @click="addDigit(8)">8</b-button></b-col>
    <b-col class="px-2"><b-button block class="blue-knapp p-2 storan-tekst" @click="addDigit(9)">9</b-button></b-col>
    <b-col class="px-2">
      <b-button block class="blue-knapp storan-tekst p-2 "  :class="[slag == 2 ? 'gul-knapp' : '']" v-on:click="changeCalcType(2)">&times;</b-button>
      </b-col>
  </b-row>

  <b-row no-gutters class="my-1">
    <b-col class="px-2"><b-button block class="blue-knapp p-2 storan-tekst" @click="addDigit(4)">4</b-button></b-col>
    <b-col class="px-2"><b-button block class="blue-knapp p-2 storan-tekst" @click="addDigit(5)">5</b-button></b-col>
    <b-col class="px-2"><b-button block class="blue-knapp p-2 storan-tekst" @click="addDigit(6)">6</b-button></b-col>
    <b-col class="px-2">
      <b-button block class="p-2 storan-tekst blue-knapp"  :class="[slag == 1 ? 'gul-knapp' : '']" v-on:click="changeCalcType(1)">-</b-button>
      
    </b-col>
  </b-row>

  <b-row no-gutters class="my-1">
    <b-col class="px-2"><b-button block class="blue-knapp p-2 storan-tekst" @click="addDigit(1)">1</b-button></b-col>
    <b-col class="px-2"><b-button block class="blue-knapp p-2 storan-tekst" @click="addDigit(2)">2</b-button></b-col>
    <b-col class="px-2"><b-button block class="blue-knapp p-2 storan-tekst" @click="addDigit(3)">3</b-button></b-col>
    <b-col class="px-2">
      <b-button block class="p-2 storan-tekst blue-knapp" :class="[slag == 0 ? 'gul-knapp' : '']" v-on:click="changeCalcType(0)">+</b-button>
      
      </b-col>
  </b-row>

  <b-row no-gutters class="my-1">
    <b-col class="px-2"><b-button block class="p-2 storan-tekst blue-knapp" @click="negate">+/-</b-button></b-col>
    <b-col class="px-2"><b-button block class="p-2 storan-tekst blue-knapp" @click="addDigit(0)">0</b-button></b-col>
    
    <b-col class="px-2"  cols="6" >
      <b-button block class="p-2 storan-tekst text-dark" style="background: rgba(200, 200, 200, 0.2);"  >=</b-button></b-col>
  </b-row>

  

  </div>

 
</template>

<style>
.tekst-input{
  background: rgba(255, 220, 255, 0.2);
    border: 0px solid #EC87B2;
    font-size: 120%;
    padding-right: 0.5rem!important;
    padding-left: 0.5rem!important;
    border-radius: 7px;
}

.aktivt{
  border: 2px solid #fff;
}

.calcTypeIndicator{
  display: none;
    margin-top: -20px;
    width: 150px;
    background: #FBE919;
    color: white;
    height: 40px;
    line-height: 40px;
    font-size: 20px;
    margin-bottom: -20px;
    z-index: 1000;
    position: relative;
    margin-left:-15px;
    text-align: left;
    padding-left: 8px;
}
.calcResultIndicator{
     display: block;
    margin-top: 20px;
    width: 50px;
    /* background: #FBE919; */
    color: white;
    height: 40px;
    line-height: 40px;
    font-size: 20px;
    margin-bottom: -50px;
    margin-bottom: -55px;
    z-index: 1000;
    position: relative;
    margin-left: -15px;
    text-align: left;
    padding-left: 20px;
    margin-left: 10px;
}

.blue-knapp, .blue-knapp:focus, .blue-knapp:hover, .blue-knapp:active{
  background-color: rgb(116, 195, 213);
}

.gul-knapp, .gul-knapp:focus, .gul-knapp:hover, .gul-knapp:active{
  background-color: #FBE919;
}

.storan-tekst{
  font-size: 30px;
}
</style>



<script>
import TalTilOrd from '../plugins/TalTilOrd'
import {Money} from 'v-money'


export default {
  name: 'CalculatorComponent',
  components: {Money},
  computed: {
      
      fyrstaTalState(){
        return true;
      },
      fyrstaTalAsText(){
        if(isNaN(this.fyrstaTal)){
          return "";
        }
        return TalTilOrd.toWords(this.fyrstaTal);
      },
      nastaTalAsText(){
        if(isNaN(this.nastaTal)){
          return "";
        }
        return TalTilOrd.toWords(this.nastaTal);
      },
      resultAsText(){
        if(isNaN(this.result)){
          return "";
        }
        return TalTilOrd.toWords(this.result);
      },
      calcTypeText(){
        switch (this.slag) {
          case 0:
            return "+ Pluss";
            
        case 1:
            return "- Minus";
        case 2:
            return "&times; Falda við";
            case 3:
            return "&divide; Deilt við";

          default:
            return "";
        }
      },
      result(){
        switch (this.slag) {
          case 0:
            return parseInt(this.fyrstaTal) + parseInt(this.nastaTal);
            
        case 1:
            return parseInt(this.fyrstaTal) - parseInt(this.nastaTal);
        case 2:
            return parseInt(this.fyrstaTal) * parseInt(this.nastaTal);
            case 3:
            return parseInt(this.fyrstaTal) / parseInt(this.nastaTal);

          default:
            return parseInt(this.fyrstaTal) + parseInt(this.nastaTal);
        }
      }
    },
    data() {
      return {
        fyrstaTal: 0,
        nastaTal: 0,
        slag: 0,
        talInFous:0,
        price: 0,
        money: {
          decimal: ',',
          thousands: '.',
          prefix: '',
          suffix: '',
          precision: 0,
          masked: false, /* doesn't work with directive */
        }
      }
    },
    methods:{
      changeCalcType(v){
        
        this.slag = v;
        this.talInFous = 1;

      },
      addDigit(x){
        if(this.talInFous != 1){
          if(this.fyrstaTal == 0 || this.fyrstaTal == "0"){
            this.fyrstaTal = x;
          }else{
            this.fyrstaTal += ""+ x;
          }
        }else{
          if(this.nastaTal == 0 || this.nastaTal == "0"){
            this.nastaTal = x;
          }else{
            this.nastaTal += ""+x;

          }
        }
        console.log(x);
      },
      negate(){
        if(this.talInFous != 1){
          this.fyrstaTal *= -1;
        }else{
          this.nastaTal *= -1;
        }
      },
      setFocus(x){
        this.talInFous = x;
      },
      removeOne(){
        if(this.talInFous != 1){
          if(this.fyrstaTal.length > 1){

            this.fyrstaTal = this.fyrstaTal.toString().substring(0, this.fyrstaTal.length-1);
          }else{
            this.fyrstaTal = 0;
          }
        }else{
          if(this.nastaTal.length > 1){

            this.nastaTal = this.nastaTal.toString().substring(0, this.nastaTal.length-1);
          }else{
            this.nastaTal = 0;
          }
        }
      },
      ruddaAlt(){
        this.fyrstaTal = 0;
        this.nastaTal = 0;
        this.talInFous = 0;
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>
