'use strict';

//var makeOrdinal = require('./makeOrdinal');
var isFinite = require('./isFinite');
var isSafeNumber = require('./isSafeNumber');

var TEN = 10;
var ONE_HUNDRED = 100;
var ONE_THOUSAND = 1000;
var ONE_MILLION = 1000000;
var TWO_MILLION = 2000000;
var ONE_BILLION = 1000000000;           //         1.000.000.000 (9)
var TWO_BILLION = 2000000000;           //         1.000.000.000 (9)
var ONE_TRILLION = 1000000000000;       //     1.000.000.000.000 (12)
var TWO_TRILLION = 2000000000000;       //     1.000.000.000.000 (12)
var ONE_QUADRILLION = 1000000000000000; // 1.000.000.000.000.000 (15)
var MAX = 9007199254740992;             // 9.007.199.254.740.992 (15)

var LESS_THAN_TWENTY = [
    'null',
    'eitt',
    'tvey',
    'trý', 
    'fýra', 
    'fimm', 
    'seks', 
    'sjey', 
    'átta',
    'níggju', 
    'tíggju',
    'elluvu', 
    'tólv', 
    'trettan', 
    'fjúrtan', 
    'fimtan',
    'sekstan',
    'seytjan',
    'átjan',
    'nýtjan'
];

var LESS_THAN_TWENTY_BENT = [
    'null',
    'ein',
    'tvey',
    'trý', 
    'fýra', 
    'fimm', 
    'seks', 
    'sjey', 
    'átta',
    'níggju', 
    'tíggju',
    'ellivu', 
    'tólv', 
    'trettan', 
    'fjúrtan', 
    'fimtan',
    'sekstan',
    'seytjan',
    'átjan',
    'nýtjan'
];

var TENTHS_LESS_THAN_HUNDRED = [
    'null', 'tíggju', 'tjúgu', 'tredivu', 'fjøruti', 'hálvtrýss', 'trýss', 'hálvfjerds', 'fýrs', 'hálvfems'
];

/**
 * Converts an integer into words.
 * If number is decimal, the decimals will be removed.
 * @example toWords(12) => 'twelve'
 * @param {number|string} number
 * @param {boolean} [asOrdinal] - Deprecated, use toWordsOrdinal() instead!
 * @returns {string}
 */
function toWords(number) {
    var words;
    var num = parseInt(number, 10);

    if (!isFinite(num)) {
        throw new TypeError(
            'Not a finite number: ' + number + ' (' + typeof number + ')'
        );
    }
    if (!isSafeNumber(num)) {
        throw new RangeError(
            'Input is not a safe number, it’s either too large or too small.'
        );
    }
    words = generateWords(num);
    return words;
    //return asOrdinal ? makeOrdinal(words) : words;
}

function generateWords(number) {
    var remainder, word,
        words = arguments[1];

    // We’re done
    if (number === 0) {
        return !words ? 'null' : words.join(' ').replace(/,$/, '');
    }
    // First run
    if (!words) {
        words = [];
    }
    // If negative, prepend “minus”
    if (number < 0) {
        words.push('minus');
        number = Math.abs(number);
    }

    if (number < 20) {
        remainder = 0;
        word = LESS_THAN_TWENTY[number];

    } else if (number < ONE_HUNDRED) {
        remainder = number % TEN;
        word = TENTHS_LESS_THAN_HUNDRED[Math.floor(number / TEN)];
        // In case of remainder, we need to handle it here to be able to add the “-”
        if (remainder) {
            word = LESS_THAN_TWENTY_BENT[remainder] + ' og ' + word;
            remainder = 0;
        }

    } else if (number < ONE_THOUSAND) {
        remainder = number % ONE_HUNDRED;
        word = generateWords(Math.floor(number / ONE_HUNDRED)) + ' hundrað';
        if(remainder){
            word += ' og ';
        }

    } else if (number < ONE_MILLION) {
        remainder = number % ONE_THOUSAND;
        word = generateWords(Math.floor(number / ONE_THOUSAND)) + ' túsund,';

    }
    else if (number < ONE_BILLION && number < TWO_MILLION) {
        remainder = number % ONE_MILLION;
        word = generateWords(Math.floor(number / ONE_MILLION)) + ' millión,';

    }
    else if (number < ONE_BILLION) {
        remainder = number % ONE_MILLION;
        word = generateWords(Math.floor(number / ONE_MILLION)) + ' milliónir,';

    } 
    else if (number < ONE_TRILLION && number < TWO_BILLION) {
        remainder = number % ONE_BILLION;
        word = generateWords(Math.floor(number / ONE_BILLION)) + ' milliard,';

    }
    else if (number < ONE_TRILLION) {
        remainder = number % ONE_BILLION;
        word = generateWords(Math.floor(number / ONE_BILLION)) + ' milliardir,';

    }else if (number < ONE_QUADRILLION && number < TWO_TRILLION) {
        remainder = number % ONE_TRILLION;
        word = generateWords(Math.floor(number / ONE_TRILLION)) + ' billión,';

    }
     else if (number < ONE_QUADRILLION) {
        remainder = number % ONE_TRILLION;
        word = generateWords(Math.floor(number / ONE_TRILLION)) + ' billiónir,';

    } else if (number <= MAX) {
        remainder = number % ONE_QUADRILLION;
        word = generateWords(Math.floor(number / ONE_QUADRILLION)) +
        ' billiard,';
    }

    words.push(word);
    return generateWords(remainder, words);
}

module.exports = toWords;