<template>
<section>
  <games-selector-component v-if="!selectedGame" v-on:selectGame="onSelectGame"></games-selector-component>

  <numbers-from-letters v-if="selectedGame == 'velrottutolini'"/>


  </section>
</template>

<script>

import GamesSelectorComponent from './games/GamesSelector.vue'
import NumbersFromLetters from './games/NumbersFromLetters.vue';


export default {
  name: 'GamesComponent',

  components:{
    GamesSelectorComponent,
    NumbersFromLetters
  },
  data () {
    return {
      counter: 0,
      selectedGame: null, // This value is set to the value emitted by the child
    }
  },
  methods:{
    onSelectGame(x){
      this.selectedGame = x;
    }
  }
}
</script>

